.project-container {
  justify-content: center;
  max-width: 2000px;
  text-align: center;
  align-items: center;
  align-content: center;
  padding: 30px 40px;
}
.project-title {
  padding-top: 20px;
  font-weight: bolder;
  font-size: 35px;
  margin-bottom: 20px;
}
.project__individual__wrap {
  padding-bottom: 30px;
}
.project__description__wrap {
  padding: 20px;
}
.project__description__wrap > h3 {
  margin-top: 10px;
  font-weight: 600;
  font-size: 30px;
}
.project__description__wrap > p {
  font-size: 20px;
  line-height: 30px;
}

.project__description__icon__wrap {
  overflow-y: auto;
  width: 100%;
  display: flex;
  padding: 5px;
  justify-content: space-around;
}
.devicon {
  font-size: 3rem;
  margin: 7px;
}
a {
  text-decoration: none;
  color: black;
  font-size: 18px;
  font-weight: 600;
}

.links {
  clear: both;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.links > div {
  margin: 10px;
  background-color: rgb(201, 164, 164);
  border-radius: 10px;
  padding: 8px 10px;
}
@media screen and (min-width: 960px) {
  .project__individual__wrap {
    padding-right: 200px;
    padding-left: 200px;
  }
  .project__description__wrap {
    padding: 100px;
  }
}
@media screen and (min-width: 1700px) {
  .project-title {
    padding-top: 40px;
  }
  .project__individual__wrap {
    padding-right: 250px;
    padding-left: 250px;
    margin: 50px 0;
  }
  .slide {
    width: 110px;
  }
  .project__description__wrap {
    padding: 100px;
  }
  .project__description__wrap > p {
    font-size: 25px;
  }
}
